import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    t: null,
    idC: null,
    idCl: null,
    isConnected: false,
    mensajeError: false,
  },
  mutations: {
    SOCKET_CONNECT(state) {
      console.log('--------1')
      state.isConnected = true;
    },

    SOCKET_DISCONNECT(state) {
      console.log('--------2')
      state.isConnected = false;
    },
    SOCKET_ERROR(state) {
      console.log('--------3')
      state.isConnected = false;
    },
    SOCKET_RECONNECTING(state) {
      console.log('--------4')
      state.isConnected = false;
    },
    /*  SOCKET_in_(state, message) {
         console.log('--------5')
         console.log(message)
     }, */
    // SOCKET_DATACONDUCTOR(state, message) {
    //   console.log('--------6')
    //   console.log(message)
    // },
    // SOCKET_dataConductor(state, message) {
    //   console.log('--------6')
    //   console.log(message)
    // },
    setSocket: (state, socket) => {
      state.io = socket;
      console.log('SOCKET CONECTADO')
    },
    verificarError: (state) => {
      state.mensajeError = true;
    }

  },
  actions: {
    autenticar: async ({ state, rootState }, { token }) => {
      console.log(state)
      var r = await new Promise(function (resolve, reject) {
        rootState.io.emit(process.env.VUE_APP_SOCKET_EMIT_AUTENTICAR, { token: token }, function (response) {
          console.log(token)
          if (response.en === 1)
            resolve(response);
          else
            reject();
        });
      });
      return (r)
    },
  },
  modules: {},
});
