<script>
import Mapbox from "mapbox-gl";
import { MglMap, MglMarker } from "vue-mapbox";

export default {
  components: {
    MglMap,
    MglMarker
  },
  data() {
    return {
      accessToken: "pk.eyJ1IjoibWFudWVsOTQiLCJhIjoiY2sweHlsNWozMDUzMTNucW5mdDZlOXJzNSJ9.WH8MEy5frVRM8ejqs-xebg", // your access token. Needed if you using Mapbox maps
      style: "mapbox://styles/mapbox/streets-v9",
      center: [-79.2081012, -3.9961385],
      callePrincipal: "",
      calleSecundaria: "",
      solicitudes: [],
      loading: false,
      mensajeSolicitud: "",
      mensaje: false,
      coordinates: [-79.2081012, -3.9961385],
      dialog: false,
      calificacionCliente: '',
      comentarioCliente: '',
      exampleRules: [
        v => !!v || "Campo requerido",
        v => ( v && v <= 5 ) || "No puede ser mayor a 5"
      ],
      solicitudS: [],
      loadingF: false
      // firstNameRules: [
      //   value => {
      //     if (value?.length > 3) return true
      //
      //     return 'First name must be at least 3 characters.'
      //   },
      // ],
      // lastName: '123',
      // lastNameRules: [
      //   value => {
      //     if (/[^0-9]/.test(value)) return true
      //     return 'Last name can not contain digits.'
      //   },
      // ],
    };
  },
  created() {
    // We need to set mapbox-gl library here in order to use it in template
    this.mapbox = Mapbox;
  },
  methods: {
    solicitarTaxi() {
      if (this.callePrincipal === "" || this.calleSecundaria === "")
        return self.openNotification(
          "",
          2,
          "Campos Obligatorios",
          "Por favor, ingrese todos los campos"
        );
      this.loading = true;
      console.log("Solicitando Taxi");
      var self = this;

      let headersList = {
        Accept: "/",
        version: "1.0.0",
        "Content-Type": "application/x-www-form-urlencoded"
      };
      const params = new URLSearchParams();
      params.append("callePrincipal", this.callePrincipal);
      params.append("calleSecundaria", this.calleSecundaria);
      params.append("token", this.$store.state.t);
      let reqOptionsG = {
        url:
          process.env.VUE_APP_API_GETEWAY + ":" +
          process.env.VUE_APP_API_SOLICITAR_PORT +
          process.env.VUE_APP_SOLICITAR,
        withCredentials: true,
        credentials: "include",
        method: "POST",
        headers: headersList,
        data: params
      };

      this.axios(reqOptionsG)
        .then(function(response) {
          console.log(response);
          if (response.data.en == 1) {
            self.loading = false;
            console.log(response);
            self.mensaje = true;
            self.openNotification(
              "",
              3,
              "Solicitud creada",
              "Estimada cliente su solicitud fue atendida con éxito, espere mientras le asignamos un conductor"
            );
            // self.solicitudes.push(response.data.conductor);
            // console.log(self.solicitudes);

          } else {
            self.loading = false;
            self.openNotification(
              "",
              2,
              "Error solicitud",
              response.data.m
            );
            // self.deleteSesion();
          }
        })
        .catch(function(error) {
          console.log(error);
          self.loading = false;
          self.openNotification(
            "",
            2,
            "Error solicitud",
            "No se logro generar la solicitud, por favor, inténtelo más tarde"
          );
          // self.deleteSesion();
        })
        .finally(() => {
        });
    },
    openNotification(position = null, color, titulo, texto) {
      var c;
      if (color === 1) c = "primary";
      else if (color == 2) c = "danger";
      else if (color == 3) c = "success";
      else if (color == 4) c = "warn";
      else c = "rgb(59,222,200)";

      const noti = this.$vs.notification({
        progress: "auto",
        color: c,
        position,
        title: titulo,
        text: texto
      });
      console.log(noti);
    },

    finalizarSolicitud(solicitud) {
      console.log("Finalizando la solicitud");
      console.log(solicitud);
      this.dialog = true;
      this.solicitudS.push(solicitud)
    },
    finalizar(){
      console.log(this.solicitudS)
      this.loadingF = true;
      var self = this;

      let headersList = {
        Accept: "/",
        version: "1.0.0",
        "Content-Type": "application/x-www-form-urlencoded"
      };
      const params = new URLSearchParams();
      params.append("token", this.$store.state.t);
      params.append("idSolicitud", this.solicitudS[0].idSolicitud);
      params.append("calificacionCliente", this.calificacionCliente);
      params.append("comentarioCliente", this.comentarioCliente);
      let reqOptionsG = {
        url:
          process.env.VUE_APP_API_GETEWAY + ":" +
          process.env.VUE_APP_API_GETEWAY_PORT +
          process.env.VUE_APP_SOLICITAR_FINALIZAR,
        withCredentials: true,
        credentials: "include",
        method: "POST",
        headers: headersList,
        data: params
      };

      this.axios(reqOptionsG)
        .then(function(response) {
          console.log(response);
          if (response.data.en == 1) {
            self.loading = false;
            self.loadingF = false;
            console.log(response);
            self.openNotification(
              "",
              3,
              "Solicitud finalizada",
              "Estimada cliente su solicitud fue finalizada con éxito"
            );
            console.log(self.solicitudes.length)
            for (var i=0; i<=self.solicitudes.length; i++) {
              console.log('Eliminando la solicitud')
              if (self.solicitudes[i].idSolicitud === self.solicitudS[0].idSolicitud) {
                self.solicitudes.splice(i, 1);
                console.log('Solicitud borrada')
                self.solicitudS = [];
                self.dialog = false;
                self.calificacionCliente = '';
                self.comentarioCliente = '';
              }
            }
          } else {
            self.loading = false;
            self.loadingF = false;
            self.openNotification(
              "",
              2,
              "Solicitud finalizada",
              "No se logro finalizar la solicitud, inténtelo más tarde"
            );
            // self.deleteSesion();
          }
        })
        .catch(function(error) {
          console.log(error);
          this.loading = false;
          self.loadingF = false;
          self.openNotification(
            "",
            2,
            "Solicitud finalizada",
            "No se logro finalizar la solicitud, inténtelo más tarde"
          );
          // self.deleteSesion();
        })
        .finally(() => {
        });
    }
  },
  sockets: {
    dataConductor(data) {
      console.log(
        "RESPUESTA DEL SEGVIFOR DE la data del conductor -------------------"
      );
      console.log(data)
      if(data.en == 1) {
        this.openNotification(
          "",
          3,
          "Conductor asignado",
          "Estimado cliente, su solicitud fue asignada"
        );
        this.solicitudes.push(data)
        this.mensaje = false;
      } else {
        this.openNotification(
          "",
